import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Feedback from "../views/Feedback";
import Offer from "../views/Offer";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/feedback',
    name: 'Feedback',
    component: Feedback,
  },
  {
    path: '/offer',
    name: 'Offer',
    component: Offer,
  },
]

const router = new VueRouter({
  routes
})

export default router
