<template>
  <div class="home-page">
    <div class="home-page-translator">
      <div class="home-page-translator-note">{{ toLangFormatted }}</div>
      <div class="home-page-translator-word">{{ translated || '-' }}</div>
      <div class="home-page-translator-note">{{ fromLangFormatted }}</div>
      <input
          class="home-page-translator-input"
          v-model="fromInput"
          @keydown.space.prevent
          @paste.prevent
          ref="fromInput"
      />
      <v-btn
        class="mt-8"
        text
        @click="redirectTo({ name: 'Offer' })"
      >Предложить перевод</v-btn>
    </div>
    <v-btn
      v-for="button in floatingButtons"
      :key="button.key"
      v-bind="{
        ...baseFloatingButtonsProps,
        ...button.bind
      }"
      v-on="button.on"
    >
      <v-icon large>{{ button.icon }}</v-icon>
    </v-btn>
  </div>
</template>

<script>

import {EN_DICT, RU_DICT} from "../ext";

const EN = EN_DICT()
const RU = RU_DICT()

const randomInt = max => Math.floor(Math.random() * max)

const randomArrayValue = arr => arr[randomInt(arr.length)]

const LS_FROM_LANG_KEY = 'fromLang'
const FROM_LANG = Object.freeze({
  EN: 'en',
  RU: 'ru',
})

export default {
  name: 'Home',
  data: () => ({
    fromLang: localStorage.getItem(LS_FROM_LANG_KEY) || 'en',
    fromInput: '',
    translated: '-',
  }),
  watch: {
    fromInput(val) {
      this.translate(val.toLowerCase())
    },
    fromLang(val) {
      localStorage.setItem(LS_FROM_LANG_KEY, val)
    },
  },
  created() {
    this.reset()
    this.$nextTick().then(this.focusOnInput)
  },
  computed: {
    toLangFormatted() {
      switch (this.fromLang) {
        case FROM_LANG.EN:
          return 'На Русский'
        case FROM_LANG.RU:
          return 'На Английский'
      }
    },
    fromLangFormatted() {
      switch (this.fromLang) {
        case FROM_LANG.RU:
          return 'С Русского'
        case FROM_LANG.EN:
          return 'С Английского'
      }
    },
    baseFloatingButtonsProps() {
      return {
        elevation: 2,
        fab: true,
        fixed: true,
        bottom: true,
        color: 'primary',
      }
    },
    floatingButtons() {
      return [
        {
          icon: 'mdi-dice-multiple',
          bind: {
            key: 0,
            left: true,
          },
          on: {
            click: this.reset,
          },
        },
        {
          icon: 'mdi-swap-horizontal',
          bind: {
            key: 1,
            right: true,
          },
          on: {
            click: this.switchLang,
          },
        },
      ]
    },
    currentDictionary() {
      switch (this.fromLang) {
        case FROM_LANG.RU:
          return RU
        case FROM_LANG.EN:
          return EN
        default:
          return EN
      }
    },
  },
  methods: {
    redirectTo(to) {
      this.$router.push(to).catch(() => {})
    },
    focusOnInput() {
      this.$refs.fromInput.focus()
    },
    getRandomWord() {
      const words = Object.keys(this.currentDictionary)

      return randomArrayValue(words)
    },
    switchLang() {
      switch (this.fromLang) {
        case FROM_LANG.RU:
          this.fromLang = FROM_LANG.EN
          break
        case FROM_LANG.EN:
          this.fromLang = FROM_LANG.RU
          break
        default:
          this.fromLang = FROM_LANG.RU
          break
      }

      this.fromInput = this.translated
    },
    resetTranslated() {
      this.translated = ''
    },
    reset() {
      this.fromInput = this.getRandomWord()
      this.resetTranslated()
    },
    translate(val) {
      if (val in this.currentDictionary) {
        this.translated = this.currentDictionary[val]
      } else {
        this.resetTranslated()
      }
    },
  },
}
</script>

