<template>
  <div id="offer-page">
    <t-page title="Предложить перевод">
      <template v-if="!success">
        <t-form
            :fields="fields"
            :is-loading="isLoading"
            btn-text="Предложить"
            @submit="handleSubmit"
        />
      </template>
      <template v-else>
        <TSuccessCard
          title="Вы предложили перевод"
          description="Мы рассмотрим его в течении некоторого времени"
        />
      </template>
    </t-page>
  </div>
</template>

<script>
import TForm from "../components/TForm";
import TPage from "../components/TPage";
import {getValidators, VALIDATORS_NAMES} from "../ext/validators";
import { handleRequest } from "../ext";
import TSuccessCard from "../components/TSuccessCard";

export default {
  name: 'Offer',
  data: () => ({
    success: false,
    isLoading: false,
    fields: [
      {
        id: 0,
        component: 'v-text-field',
        name: 'en-word',
        props: {
          label: 'Английский',
          required: true,
          rules: getValidators(VALIDATORS_NAMES.EN_WORD),
        },
      },
      {
        id: 1,
        component: 'v-text-field',
        name: 'ru-word',
        props: {
          label: 'Русский',
          required: true,
          rules: getValidators(VALIDATORS_NAMES.RU_WORD),
        },
      },
    ],
  }),
  methods: {
    async handleSubmit() {
      this.isLoading = true

      const response = await handleRequest()

      this.isLoading = false
      this.success = true
    }
  },
  components: {
    TForm,
    TPage,
    TSuccessCard
  },
}
</script>

